<template>
  <el-container>
    <el-header> Online Exercise </el-header>
    <el-main>
      <div class="timer">{{ time }}s</div>
      <el-form>
        <el-form-item>{{ questionAnswer.stem }}</el-form-item>
        <el-form-item>
          <div class="options">
            <el-checkbox
              v-for="i in 5"
              :key="i"
              v-model="answer[i - 1]"
              :label="questionAnswer.options[i - 1]"
            />
          </div>
        </el-form-item>
        <el-form-item class="submit-button">
          <el-button type="primary" size="large" @click="submitAnswer" round
            >Submit</el-button
          >
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
import axios from "axios";
import { ElMessage, ElMessageBox } from "element-plus";

export default {
  name: "foreground",
  data() {
    return {
      answer: [false, false, false, false, false],
      exam: {},
      questionAnswer: {},
      examToQuestion: {},
      time: 3600,
    };
  },
  created() {
    this.setTimer();
    this.beginExam();
  },
  methods: {
    beginExam() {
      this.exam.uid = this.$store.state.user.id;
      this.exam.startTime = new Date().getTime();
      this.exam.score = 0;
      this.exam.finished = false;
      axios.post("/foreground", this.exam).then((res) => {
        if (res.data.code === 0) {
          ElMessage.success(res.data.msg);
          this.exam = res.data.data;
          this.time -= parseInt(
            (new Date().getTime() - this.exam.startTime) / 1000
          );
          this.examToQuestion.eid = this.exam.id;
          this.examToQuestion.qid = 0;
          this.examToQuestion.score = 0;
          axios.put("/foreground", this.examToQuestion).then((res) => {
            if (res.data.code === 0) {
              if (res.data.data === null) this.endExam();
              this.questionAnswer = res.data.data;
              this.examToQuestion.qid = this.questionAnswer.qid;
            } else ElMessage.error(res.data.msg);
          });
        } else ElMessage.error(res.data.msg);
      });
    },
    submitAnswer() {
      this.examToQuestion.score = 2;
      if (
        !this.answer[0] &&
        !this.answer[1] &&
        !this.answer[2] &&
        !this.answer[3] &&
        !this.answer[4]
      )
        this.examToQuestion.score = 0;
      else
        for (var i = 0; i < 5; i++)
          if (this.questionAnswer.answers[i] && !this.answer[i])
            this.examToQuestion.score = 1;
          else if (this.answer[i] && !this.questionAnswer.answers[i]) {
            this.examToQuestion.score = 0;
            break;
          }
      axios.put("/foreground", this.examToQuestion).then((res) => {
        if (res.data.code === 0) {
          if (res.data.data === null) this.endExam();
          this.questionAnswer = res.data.data;
          this.examToQuestion.qid = this.questionAnswer.qid;
          this.answer = [false, false, false, false, false];
        } else ElMessage.error(res.data.msg);
      });
    },
    endExam() {
      axios.delete("/foreground/" + this.exam.id).then((res) => {
        if (res.data.code === 0) {
          ElMessageBox.alert(
            "The exercise is over. You got " + res.data.data + " points."
          );
          this.$router.replace("/login");
        } else ElMessage.error(res.data.msg);
      });
    },
    setTimer() {
      var end = setInterval(() => {
        this.time--;
        if (this.time <= 0) {
          this.endExam();
          clearInterval(end);
        }
      }, 1000);
    },
  },
};
</script>

<style scoped>
.options {
  display: flex;
  flex-direction: column;
}
.submit-button {
  display: inline-flex;
  text-align: center;
}
.timer {
  float: right;
  font-size: 50px;
}

.el-container {
  height: 100%;
}

.el-header {
  background-color: #b3c0d1;
  color: var(--el-text-color-primary);
  text-align: center;
  line-height: 60px;
  font-size: 50px;
}

.el-main {
  background-color: #e9eef3;
  color: var(--el-text-color-primary);
  text-align: center;
  line-height: 80px;
}

.el-form {
  word-break: break-word;
  white-space: pre-wrap;
  margin: 0px 200px;
}

.el-form-item {
  text-align: left;
  margin: 20px 0px;
  --font-size: 20px;
}

.el-checkbox {
  margin: 20px 0px;
  --el-checkbox-font-size: 20px;
}
</style>
